import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { EnumeraService } from '../../providers/enumera-request';
import { LoadingController  } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';

@Component({
  selector: 'page-tracking-system',
  templateUrl: 'tracking-system.html'
})
export class TrackingSystemPage {
  public search1; search2; search3: string = '';
  public items; matricole; documenti: any;
  public loading: any;

  constructor(public navCtrl: NavController, private enumeraService:EnumeraService, public loadingController:LoadingController, public params:NavParams, public alertCtrl:AlertController, private inappbrowser: InAppBrowser) {
    this.search1 = "";
    this.search2 = "";
    this.search3 = "";
  }
  
  getdata(search1,search2,search3){
    this.showLoading(); 
    this.enumeraService.requestStr = 'prgname=getLottoProduzione&param[cod_articolo]=' + this.search1 + '&param[cod_lotto]=' + this.search2;
    this.enumeraService.doRequest();
    this.enumeraService.getDataSet().subscribe(
     result => {
      if (result != null){

        if (result["error"] != 'auth-error' && result["error"] !=null){
          this.presentAlert('Attenzione', result["error"]);
        }  
        else if (result["error"] != 'auth-error'){
          this.items = result;
          this.matricole = this.items[0].matricole;
          this.documenti = this.items[0].documenti;
        }
        else {
          this.presentAlert("Attenzione","Utente non autorizzato a questa funzione. Contattare il customer service per l'attivazione");
        }
      }
      
     },
     err =>{
       console.error("Error : "+err);
     } ,
     () => {
       console.log('getData completed');
       console.log(this.items);
         //this.loading.dismiss();
       this.hideLoading();
     }
   );
 
 }
 
 private showLoading() {
  this.loading = this.loadingController.create({
    content: "Please wait..."
  });
  this.loading.present();
  setTimeout(() => {
    this.loading.dismiss();
    }, 20000);
}

private hideLoading(){
  this.loading.dismiss();
}

presentAlert(titolo, messaggio) {
 let alert = this.alertCtrl.create({
   title: titolo,
   subTitle: messaggio,
   buttons: ['Ok']
 });
 alert.present();
}

scaricaFile(documento) {
  const browser = this.inappbrowser.create(documento.url, '_blank', 'location=yes')
}
}



