import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { EnumeraService } from '../../providers/enumera-request';
import { Platform } from 'ionic-angular';
import { LoginPage } from '../login/login';
import { LoadingController  } from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  slides;config : any = 0;
  testi;notifiche: any;
  testo_home: any;
  public loading: any;
  
  constructor(public navCtrl: NavController,private enumeraService:EnumeraService,public plt: Platform, public loadingController:LoadingController) {
     if (localStorage.getItem("config") === null) {
         this.navCtrl.push(LoginPage);
      }
      else {

          this.config = JSON.parse(localStorage.getItem("config"));
          this.showLoading(); 
          this.loadTesto();
          this.loadSlides();
          this.testo_home = {"TESTO":"Benvenuto in RS"};
    }
  }  
  
  loadTesto(){
   this.enumeraService.requestStr = 'prgname=getTestiBase&viewname=testo_home';
   this.enumeraService.doRequest();
   this.enumeraService.getDataSet().subscribe(
    result => {
      this.testi = result;
      this.testo_home = this.testi[0].TESTO;
    },
    err =>{
      console.error("Error : "+err);
    } ,
    () => {
      
      console.log('getData completed');
      console.log(this.testi);
    }
  );


  }

  loadSlides(){
   this.enumeraService.requestStr = 'prgname=getHomeSlides&viewname=immagini';
   this.enumeraService.doRequest();
   this.enumeraService.getDataSet().subscribe(
    result => {
      this.slides = result;

    },
    err =>{
      console.error("Error : "+err);
    } ,
    () => {
      console.log('getData completed');
      this.hideLoading();
    }
  );
    
}

private showLoading() {
  this.loading = this.loadingController.create({
    content: "Please wait..."
  });
  this.loading.present();
  setTimeout(() => {
    this.loading.dismiss();
    }, 20000);
}

private hideLoading(){
  this.loading.dismiss();
}

}



