import { Component } from '@angular/core';
import { NavController, NavParams, Item } from 'ionic-angular';
import { EnumeraService } from '../../providers/enumera-request';
import { AlertController } from 'ionic-angular';

@Component({
  templateUrl: 'articolo.html',
  providers: [EnumeraService]
})


export class ArticoloPage {
  public item; items_details: any = 0;  
  constructor(public navCtrl: NavController, public params:NavParams, private enumeraService:EnumeraService, public alertCtrl:AlertController) {
    this.item = params.get('item');
    this.enumeraService.requestStr = 'prgname=getArticolo&param[codice]=' + this.item.CODART;
    this.enumeraService.doRequest();
    this.getdata();
    
  }

    getdata(){
      this.enumeraService.getDataSet().subscribe(
      result => {
        this.items_details = result;

    },
    err =>{
      console.error("Error : "+err);
    } ,
    () => {
      console.log('getData completed');
    }
  );

  
  }

  prenotaMatricola(item){
    this.enumeraService.requestStr = 'prgname=prenotaMatricola&param[idmatricola]=' + item.matricola;
    this.enumeraService.doRequest();   
    this.enumeraService.getDataSet().subscribe(
      result => {
        if (result != null){
          if (result["status"] != 'NACK'){
            this.presentAlert("Invio email", "La sua richiesta è stata registrata con successo. A breve sarete contattati dal customers service") 
          }
          else {
            this.presentAlert("Attenzione","La sua richiesta non è andata a buon fine. Contattare il customer service");
          }
        }
  
    },
    err =>{
      console.error("Error : "+err);
      this.presentAlert("Invio email", "Errore sistema")  
    } ,
    () => {
 
      console.log('getData completed');     
    }

    );

  }

  presentAlert(titolo, messaggio) {
    let alert = this.alertCtrl.create({
      title: titolo,
      subTitle: messaggio,
      buttons: ['Ok']
    });
    alert.present();
  }
  

}



