import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Http } from '@angular/http';
import { EnumeraService } from '../../providers/enumera-request';
import { LoadingController  } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { AlertController } from 'ionic-angular';


@Component({
  selector: 'page-certificati',
  templateUrl: 'certificati.html'
})
export class CertificatiPage {
  public items: any;
  public loading: any;

  constructor(public navCtrl: NavController,private enumeraService:EnumeraService,public loadingController:LoadingController, private inappbrowser: InAppBrowser, public alertCtrl:AlertController) {
    this.getdata('');
  }

  getdata(folder){
   this.showLoading(); 
   this.enumeraService.requestStr = 'prgname=getEnuCertificati&param[folder]=' + folder ;
   this.enumeraService.doRequest();
   this.enumeraService.getDataSet().subscribe(
    
    result => {
      
      if (result != null){
        if (result["error"] != 'auth-error'){
          this.items = result;
        }
        else {
           this.presentAlert("Attenzione","Utente non autorizzato a questa funzione. Contattare il customer service per l'attivazione");
        }
      }
      
     // console.log(result);
    },
    err =>{
      console.error("Error : "+err);
    } ,
    () => {
      console.log('getData completed');
      //this.loading.dismiss();
      this.hideLoading();

    }
  );

}

private showLoading() {
  this.loading = this.loadingController.create({
    content: "Please wait..."
  });
  this.loading.present();
  setTimeout(() => {
    this.loading.dismiss();
    }, 2000);
}

private hideLoading(){
  this.loading.dismiss();
}

subDir(item){
  if (item.tipo == 'D'){
    this.getdata(item.nome);
  }
  else {
    
    this.download(item)
  };
} 

download(item) {
  const browser = this.inappbrowser.create(item.url, '_blank', 'location=yes')
}

presentAlert(titolo, messaggio) {
  let alert = this.alertCtrl.create({
    title: titolo,
    subTitle: messaggio,
    buttons: ['Ok']
  });
  alert.present();
}

}



