import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { HttpModule} from '@angular/http';
import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { TrackingSystemPage } from '../pages/tracking-system/tracking-system';
import { CertificatiPage } from '../pages/certificati/certificati';
import { MagazzinoPage } from '../pages/magazzino/magazzino';
import { LoginPage } from '../pages/login/login';
import { ArticoloPage } from '../pages/articolo/articolo';
import { MatricolaPage } from '../pages/matricola/matricola';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { EmailComposer } from '@ionic-native/email-composer';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    TrackingSystemPage,
    CertificatiPage,
    MagazzinoPage,
    LoginPage,
    ArticoloPage,
    MatricolaPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    TrackingSystemPage,
    CertificatiPage,
    ArticoloPage,
    MagazzinoPage,
    LoginPage,
    MatricolaPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    InAppBrowser,
    EmailComposer,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}


