import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { EnumeraService } from '../../providers/enumera-request';
import { ArticoloPage } from '../articolo/articolo';
import { LoadingController  } from 'ionic-angular';
import { AlertController } from 'ionic-angular';

@Component({
  selector: 'page-magazzino',
  templateUrl: 'magazzino.html'
})
export class MagazzinoPage {
  public searchTerm: string = '';
  public items: any;
  public loading: any;

  constructor(public navCtrl: NavController, private enumeraService:EnumeraService, public loadingController:LoadingController, public params:NavParams, public alertCtrl:AlertController) {
    this.getdata('');
  }

  getdata(searchTerm){
    this.showLoading(); 
    this.enumeraService.requestStr = 'prgname=getCatalogo&viewname=catalogo&param[flg_matricole]=1&param[flg_solo_giacenti]=1&param[codice]=' + this.searchTerm;
    this.enumeraService.doRequest();
    this.enumeraService.getDataSet().subscribe(
     result => {
      if (result != null){
        if (result["error"] != 'auth-error'){
          this.items = result;
        }
        else {
          this.presentAlert("Attenzione","Utente non autorizzato a questa funzione. Contattare il customer service per l'attivazione");
        }
      }
      
     },
     err =>{
       console.error("Error : "+err);
     } ,
     () => {
       console.log('getData completed');
       //console.log(this.items);
         //this.loading.dismiss();
       this.hideLoading();
        // se trovato un solo salta al dettaglio articolo
       if (this.items != null){
          if (this.items.length === 1){
           this.vediArticolo(this.items[0])
         }
      }
     }
   );
 
 }
 
 vediArticolo(item){
   this.navCtrl.push(ArticoloPage, {item: item})
 } 
 
 private showLoading() {
     this.loading = this.loadingController.create({
       content: "Please wait..."
     });
     this.loading.present();
     setTimeout(() => {
       this.loading.dismiss();
       }, 20000);
   }
 
   private hideLoading(){
     this.loading.dismiss();
   }
   
   presentAlert(titolo, messaggio) {
    let alert = this.alertCtrl.create({
      title: titolo,
      subTitle: messaggio,
      buttons: ['Ok']
    });
    alert.present();
  }

}



