import { Component, ViewChild } from '@angular/core';
import { Platform, Nav, MenuController } from 'ionic-angular';
import { AlertController } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { EmailComposer } from '@ionic-native/email-composer';
import { EnumeraService } from '../providers/enumera-request';
import { TrackingSystemPage } from '../pages/tracking-system/tracking-system';
import { CertificatiPage } from '../pages/certificati/certificati';
import { MagazzinoPage } from '../pages/magazzino/magazzino';
import { ArticoloPage } from '../pages/articolo/articolo';
import { MatricolaPage } from '../pages/matricola/matricola';
import { LoginPage } from '../pages/login/login';
import { HomePage } from '../pages/home/home';



@Component({
  templateUrl: 'app.html',
    providers: [EnumeraService]
})
export class MyApp {
  @ViewChild(Nav) navCtrl: Nav;
    rootPage:any = HomePage;
    showedAlert: boolean;    

  constructor(statusBar: StatusBar, splashScreen: SplashScreen, public menu: MenuController, public platform: Platform, public alertCtrl: AlertController) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
      platform.registerBackButtonAction(() => {
        if(this.menu.isOpen()){
           this.menu.close()
        } 
        else if(this.navCtrl.canGoBack()){
          this.navCtrl.pop();
        }else{
            let alert = this.alertCtrl.create({
              title: 'Conferma',
              message: 'Confermi di voler uscire?',
              buttons: [{
                text: "Si, esci",
                handler: () => { this.platform.exitApp() }
              }, {
                text: "No, rimani",
                role: 'cancel'
              }]
            })
            alert.present();
            //don't do anything
        }
      });
          
    });

  }

 
  goToHome(params){
    if (!params) params = {};
    this.navCtrl.setRoot(HomePage);
  }goToTrackingSystem(params){
    if (!params) params = {};
    this.navCtrl.setRoot(TrackingSystemPage);
  }goToCertificati(params){
    if (!params) params = {};
    this.navCtrl.setRoot(CertificatiPage);
  }goToMagazzino(params){
    if (!params) params = {};
    this.navCtrl.setRoot(MagazzinoPage);
  }goToMatricola(params){
    if (!params) params = {};
    this.navCtrl.setRoot(MatricolaPage);
  }goToLogin(params){
    if (!params) params = {};
    this.navCtrl.setRoot(LoginPage);
  }

exitApp(){
  this.platform.exitApp();
}
}



